import React, {Component} from 'react';

const  imageUrl = 'data:image/png;base64,';
class Logo extends Component {
 
    render() { 
        return ( 
            <header >
            <div>
                <img className="logo" src={imageUrl} alt=""/>                
            </div>
            </header>
         );
    }
}
 
export default Logo;